import uniPopup from "@/components/uni-popup/uni-popup.vue"
var graceJS = require('../../common/grace.js');
var api = require('../../common/api.js')
var _self
export default {
	data() {
		return {
			host: api.host,
			item: [],
			textare: '',
			showimgs: [],
			master: {},
			comment: [],
			uid: api.userid,
			type: '',
			id: 0,

		}
	},
	components: {
		uniPopup
	},
	onBackPress() {
		if (this.type !== '') {
			this.type = '';
			return true;
		}
	},
	onShow() {
		this.getdb(this.id);
	},
	onLoad(option) {
		this.id = option.id;

		this.master = api.mainlist.master;
		console.log(api.askitem, api.userInfo);
		let imgs = api.askitem.showimgs;
		if (!this.showimgs) {
			return false
		};
		let host = api.host;

		graceJS.arrayEach(imgs, (item) => {
			console.log(item);
			this.showimgs.push(host + item);

		});
		console.log(this.showimgs, host, '图片组');


	},
	methods: {
		togglePopup(type) {
			this.type = type;

		},

		pl() {

			uni.navigateTo({
				url: '../ask/comment'
			})



		},

		async addcomment(arr) {
			let db = await api.GET('addask', arr);
			console.log(db, 'DDDD');
			if (db.code == 1) {

				api.msg('操作成功！');
				this.item.addask = this.textare;
				this.togglePopup("");


			}






		},

		addcomm() {
			if (this.textare.length < 3) {
				api.msg('评论不符合要求')
				return false
			};
			let ob = {};
			let da = new Date();
			ob['id'] = this.id;
			ob['addask'] = this.textare;
			ob['date'] = da.getFullYear() + '-' + (da.getMonth() + 1) + '-' + da.getDate();
			this.addcomment(ob);

		},

		async getdb(id) {

			uni.showLoading && uni.showLoading({
				title: '加载中...'
			});

			let db = await api.GET('getidask', {
				id: id
			});
			console.log(db);
			this.item = db.data;
			if (db.data.comment) {
				this.comment = db.data.comment;
			}

			uni.hideLoading();


		},









	}
}
